import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import LoaderComponent from '../components/Loader'
import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import Slider from "react-slick";
import axios from 'axios'
import {faChevronRight} from '@fortawesome/free-solid-svg-icons';
import {frontConfig} from '../settings/frontconfig'

class Testimonials extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            location: window.location.pathname,
            isLoaded:false,
            data:[],
            user:'',
            message:'',
        };
    }
    componentDidMount(){
        this.fetchData();
    }
    fetchData = async () => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getComments )
            .then((result) => {
                console.log(result)
                    this.setState({isLoaded: true,  data: result.data});
            }, (error) => {
                this.setState({isLoaded: false, error});
            })
    }

    
    
    submitForm = async (e) => {

        e.preventDefault();
        const {user, message} = this.state;
        let bodyFormData = new FormData();
        bodyFormData.set('post', 1);
        bodyFormData.set('author_name', user);
        bodyFormData.set('author_email', 'methods.test.mail@gmail.com');
        bodyFormData.set('content', message);
        // let formData = JSON.stringify({
        //     post: 1,
        //     author_name: user,
        //     author_email: 'methods.test.mail@gmail.com',
        //     content: message,
        //   });
    
        await axios({
            method: 'post', 
            url: frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getComments,
            body: bodyFormData,
            headers: {
                'Content-Type': 'application/json',
            }
          }).then((res) => {
            console.log(res)
       }, (error) => {
           console.error(error)
        })
    }
    onUserChange = (event) => {
        this.setState({user: event.target.value})
  
      }
      onMessageChange=(event)=> {
        this.setState({message: event.target.value})
      
      }
    buildForm = () => {

        return (
        <form onSubmit={this.submitForm.bind(this)} method='POST'>
            <input  className="form-control w-100" type="text" placeholder="Naam" aria-describedby="nameHelp"  value={this.state.user} onChange={this.onUserChange.bind(this)}></input>
            <textarea  className="form-control w-100" rows="1" placeholder='Jouw ervaring..'   value={this.state.message} onChange={this.onMessageChange.bind(this)}></textarea>
            <div className="d-flex flex-row justify-content-end my-5">
                <button className="btn">Verstuur</button>
            </div>
        </form>);

    }
    buildTestimonial = (p,o) => {
        const settings = {
            dots: true,
            infinite: true,
            speed: 800,
            autoplaySpeed: 10000,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true
        };

        return (
        <>  
           
                <div>
                    <div className="d-flex flex-row align-items-center py-5">
                        <span className="mr-3">
                            <h2>Testimonials</h2>
                        </span>
                        <span>
                            <FontAwesomeIcon style={{ fontSize: '1.4em', color: '#FFBF37' }} icon={faChevronRight}/>
                        </span>
                    </div>

               
                    <Slider {...settings}>
                    {p.map((item, i) => {
                        // if (item.acf.message.length > 250 && this.state.location === '/') {
                        //     item.acf.message = item.acf.message.substring(0, 250) + '...'
                        // }
                    
                        return (
                       
                    <div key={i}>
                        <p className="px-5">{}</p>
                            <div className="px-5 justify-content-end d-flex">
                                <h5>{item.author_name}</h5>
                                <p dangerouslySetInnerHTML={{ __html: item.content.rendered}}></p>
                            </div>
                    </div>)})}
                    </Slider>
                </div>
                <div style={{display:o}}>
                    {this.buildForm()}
                </div>
        
            </> )
    }

    render() {
        const {data, location,isLoaded} = this.state;
        if (location === '/' && isLoaded) {
        return (
        <section className="testimonials mb-5">
            <ScrollAnimation animateIn="fadeIn">
                <div className="container">
                    {this.buildTestimonial(data,'none')}
                </div>
            </ScrollAnimation>
        </section>);
        }
        else if(isLoaded){
            return (
        <section className="testimonials contact mb-5">
            <ScrollAnimation animateIn="fadeIn">
                <div className="container">
                    {this.buildTestimonial(data)}
                </div>
            </ScrollAnimation>
        </section>);
        } else{
            return <LoaderComponent/>
        }
        
    }      

}

export default Testimonials;